<template>
    <div class="guessLikeOuter">
        <h2>猜你喜欢</h2>
        <div class="guessLikeInter">
            <div class="oneGood" @click="goToDetail(v.iid,v)" v-for="(v,i) of dataArr" :key="'item'+i">
                <div class="img-wrap">
                    <img :src="v.img" alt="">
                </div>
                <div class="good-title">
                    <div class="info">
                       {{ v.title }}
                    </div>
                    <div class="price">
                        <span>
                            <i>￥</i>{{ v.price }}</span>
                        <span class="like">{{ v.cfav }}
                            <i class="iconfont icon-shoucang"></i>
                        </span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    methods:{
        goToDetail(a,b){
            this.$router.push("/detail?itemId="+a); 
            sessionStorage.det=JSON.stringify(b); 
        }
    },
    props:{
        dataArr:null
    }
};
</script>
<style lang="less" scoped>
.guessLikeOuter {
    width: 100%;
    background-color:white;
    padding: 20px 18px; /*px*/
    > h2 {
        margin: 30px auto; /*px*/
        width: 100%;
        text-align: center;
        font-size: 28px; /*px*/
        font-weight: normal;
        background: url("/static/img/background.png") no-repeat;
        background-position: center;
        background-size: 500px 18px; /*px*/
    }
    .guessLikeInter {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        .oneGood {
            width: 348px;
            height: 614px; /*px*/
            margin-top: 18px;
            border-radius: 10px;

            .img-wrap {
                width: 348px;
                height: 464px; /*px*/
                img {
                    width: 100%;
                    height: 100%;
                }
            }
            .good-title {
                width: 100%;
                height: 150px; /*px*/
                padding: 18px; /*px*/
                .info {
                    display: -webkit-box;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient: vertical;
                    overflow: hidden;
                    line-height: 36px;
                }
                .price {
                    position: relative;
                    margin-top: 15px; /*px*/
                    .like {
                        position: absolute;
                        right: 5px;
                    }
                }
            }
        }
    }
}
</style>





