import Vue from 'vue'
import Router from 'vue-router'
import HomePage from '../components/HomePage'
import ClassifyPage from '../components/ClassifyPage'
import CartPage from '../components/CartPage'
import AboutMe from '../components/AboutMe'
import GoodDetail from '../components/GoodDetail'

import SearchList from '../components/SearchList'
import LoginPage from '../components/LoginPage'



Vue.use(Router)

const router = new Router({
  mode: "history",
  linkActiveClass: "active",
  linkExactActiveClass: "active-exact",
  routes: [
    { path: '/', redirect: "/home" },
    { path: '/home', component: HomePage },
    { path: '/classify', component: ClassifyPage },
    { path: '/cart', component: CartPage },
    { path: '/me', component: AboutMe },
    { path: '/detail', component: GoodDetail },
    { path: '/search', component: SearchList },
    { path: '/login', component: LoginPage }

  ]
})

const isLoginPage =["/cart","/me"];
router.beforeEach((to,from,next)=>{
  // let isLogin = sessionStorage.getItem("isLogin")=="true";
  let isLogin = true;
  if(isLoginPage.indexOf(to.path)!=-1&&!isLogin){
    next("/login");
  }
  next();
})
export default router;



