import jsonpUtil from "../util/jsonpUtil";
// 对远端数据的加载
// 常量应该是一个统一的数据服务地址
const mce = "https://mce.mogucdn.com/jsonp/";
const guessLikeUrl="https://list.mogujie.com/search";
const typeRight="https://mce.mogujie.com/jsonp/";
const hotSearch="https://list.mogujie.com/module/mget";
const comment="https://rate.mogujie.com/h5/rate/jsonp/x6.rate.ratelist/v1";


export default {
    // 数据的具体请求方法

    /**
     * 根据参数 pids 查询 相关数据
     * @param pids:string
     */
   
    loadDatasByPids(params){
        let url= mce+"multiget/3";
        return jsonpUtil.request(url,params)
    },
    loadGuessLike(params){
        let url=guessLikeUrl;
        return jsonpUtil.request(url,params)
    },

    loadTypeRight(params){
        let url=typeRight+"makeup/3";
        return jsonpUtil.request(url,params)
    },
    loadHotSearch(params){
        let url=hotSearch;
        return jsonpUtil.request(url,params)
    },
    loadCommentData(params){
        let url=comment;
        return jsonpUtil.request(url,params)
    }

}


