<template>
    <div>
        <div class="header">
            <a href="">
                <em class="iconfont icon-xiangzuojiantou"></em>
            </a>
            <span>购物车</span>
            <a href="">
                编辑
                <em class="iconfont icon-xiaoxizhongxin"></em>
            </a>

        </div>
        <div class="body1">
            <div class="emptyCart">
                <div>
                    <img src="/static/img/empty-cart.png" alt="">
                </div>
                <div>你的购物车空空如也</div>
                <div class="btn" @click="goHome()">
                    去逛逛
                </div>
            </div>
        </div>
        <div class="wantBuy">
            <div class="goodInfo">
                <div class="shop">
                    <span></span>
                    <span>DHSF</span>
                </div>
                <div class="good">
                    <div class="left"></div>
                    <div class="right">
                        <div>
                            <div class="img">
                                <img src="" alt="">
                            </div>
                            <div class="desc">

                            </div>
                        </div>
                    </div>

                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            // flag:this.$route.query.flag
        }
    },
    methods:{
        goHome(){
            this.$router.push("/home");
        }
    },
    mounted(){
        // console.log(this.$route.query.flag);
    },
    beforeRouteUpdate(to, from, next) {
        this.loadlistData({q:to.query.q,sort:"pop"});
        next();
    }

}
</script>
<style lang="less">
    a{
        color:#666;
        text-decoration: none;
        em{
            font-size: 30px !important;
        }
    }
    .header{
        width: 100%;
        height:90px;/*px*/
        line-height: 90px;/*px*/
        background: #fafafa;
        border-bottom:1px solid #c9c7c8;
        display: fixed;
        top:0;
        padding:0 20px;
        >a:first-child{
            display: inline-block;
            width:20%;
        }
        >span{
            display: inline-block;
            width:55%;
            text-align: center;
        }
        >a:last-child{
            display: inline-block;
            width:20%;
            text-align: right;
            font-size: 24px;/*px*/
        }
    }
    .body1{
        .emptyCart{
            background: #fff;
            width: 100%;
            text-align: center;
            height:500px;/*px*/
            font-size: 28px;/*px*/
            div:first-child{
                height:320px;/*px*/
                img{
                    margin-top:20px;/*px*/
                    height:100%;
                }
            }
            div:nth-child(2){
              margin-top:40px; /*px*/
              margin-bottom: 20px;/*px*/
            }
            .btn{
                background-color: #ff5777;
                border:0px;
                width:200px;
                height:60px;/*px*/
                text-align: center;
                line-height: 60px;/*px*/
                margin:0 auto;
                border-radius: 10px;
            }
            

        }
        .allLook{
            .look{
                font-size: 28px;/*px*/
                color: #666;
                font-weight: 400;
                margin: 30px 0 20px 30px;/*px*/
            }
            .lookgoods{
                display: flex;
                padding: 0rem 16px;/*px*/
                flex-wrap: wrap;
                justify-content: space-between;
                align-items: center;
                background-color:  #f6f6f6;
            }
        }
    }
    .wantBuy{
       
        width:100%;
        .goodInfo{
            width:100%;
            border-top:1px solid #ccc;
            border-bottom:1px solid #ccc;
            margin-top:30px;
            background-color: white;
            .shop{
                width:100%;
                height:80px;
                border-bottom:1px solid #ccc;
                span:first-child{
                    display: inline-block;
                    width:40px;
                    height:40px;
                    margin:20px 20px;
                    border:1px solid grey;
                    border-radius: 20px;
                }
                span:last-child{
                    display: inline-block;
                    width:500px;
                    height:80px;
                    line-height: 80px;
                }
                
                
            }
            .good{
                width:100%;
                height:300px;
                .left{
                    display: inline-block;
                    width:40px;
                    height:40px;
                    margin:130px 20px;
                    border:1px solid grey;
                    border-radius: 20px;
                }
                .right{
                    width:620px;
                    display: inline-block;
                    height:300px;
                    padding:30px 0px;
                    >div{
                        width:100%;
                        height:100%;
                        // background: green;
                        .img{
                            float: left;
                            width:28%;
                            height:100%;
                            // background: red;
                            margin-right:30px;
                            img{
                                width:100%;
                                height: 100%;
                            }
                        }
                        .desc{
                            float: left;
                            width:66%;
                            height:300px;
                            // background: blue;
                        }
                    }
                }
            }
        }
    }
</style>








