import api from '../../server/api';

const state = {
    areaData:[],
    sortFil:[]
}

const getters = {

}

const actions = {
    loadAreaQuery({commit},params={fcid:"",title:"",sort:"pop"}){
        console.log("==>",params);
        api.loadGuessLike({
            fcid:params.fcid,
            title:params.title,
            cKey:16,
            sort:params.sort,
            page:1
        }).then(({result})=>{
            // console.log(data);
            commit("setSortFil",result.sortFilter)
            commit("setAreaData",result.wall.docs);
        })
    },
    loadlistData({commit},params={q:"",sort:"pop"}){
        api.loadGuessLike({
            q:params.q,
            cKey:46,
            page:1,
            sort:params.sort
        }).then(({result})=>{
            commit("setSortFil",result.sortFilter)
            commit("setAreaData",result.wall.docs);
        })
    }
   
    

}

const mutations = {
   
    setHotWord(state,data){
        state.hotWord = data;
    },
    setSortFil(state,data){
        state.sortFil = data;
    },
    setAreaData(state,data){
        state.areaData = data;
    }
   
}

export default {
    state,
    actions,
    getters,
    mutations
}


