import api from '../../server/api';

const state = {
    userList:[],
    rateTags:[]
}

const getters = {

}

const actions = {
    loadComment({commit},itemId=""){
        api.loadCommentData({
            type:1,
            itemId,
            appPlat:"m"
        }).then(({ data})=>{
            commit("setRateTags",data.rateTags)
            commit("setUserList",data.list);
        })
    }
    
   
    

}

const mutations = {
    setRateTags(state,data){
        state.rateTags = data;
    },
    setUserList(state,data){
        state.userList = data;
    }
   
   
}

export default {
    state,
    actions,
    getters,
    mutations
}


