<template>
    <div>
        <search-bar></search-bar>
        <div class="clearfixed" v-show="flag">
            <scroll-view class="wrapper left">
                <div class="content">
                    <div class="content-left">
                        <div v-for="(v,i) of types.typeLeft" :key="'item'+i" @click="changetype(v['maitKey'],i,v['miniWallkey'])" :class="{a:id==i}">
                            <p>{{ v.title }}</p>
                        </div>
                    </div>
                </div>
            </scroll-view>
            <scroll-view class="wrapper right" ref="wrapper">
                <div class="content">
                    <div class="content-right">
                        <div class="types">
                            <div class="type" v-for="(v,i) of types.typeRight" :key="'item'+i" @click="loadSearchList(v.link,v.title)">
                                <div>
                                    <img :src="v.image" alt="">
                                </div>
                                <p>{{ v.title }}</p>
                            </div>
                        </div>
                        <div class="classify">
                            <ul>
                                <li v-for="(v,i) of types.sortFilter" :key="'item'+i" @click="changeListData(v.sortKey,i)" :class="{b:key==i}">{{ v.title }}</li>
                            </ul>
                        </div>
                        <div class="guessLikeInter">
                            <div class="oneGood" v-for="(v,i) of types.listData" :key="'item'+i">
                                <div class="img-wrap">
                                    <img :src="v.img" alt="">
                                </div>
                                <div class="saleCount">
                                    已售{{ v.sale }}件
                                </div>
                                <div class="good-title">
                                    <div class="info">
                                        {{ v.title }}
                                    </div>
                                    <div class="price">
                                        <span>
                                            <i>￥</i>{{ v.price }}</span>
                                        <span class="like">{{ v.cfav }}
                                            <i class="iconfont icon-shoucang"></i>
                                        </span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>
            </scroll-view>
        </div>
        <bottom-area></bottom-area>
    </div>
</template>
<script>
import SearchBar from "./SearchBar";
import BottomArea from "./BottomArea";
import BScroll from "better-scroll";
import { mapState, mapMutations, mapActions, mapGetters } from "vuex";
export default {
    data() {
        return {
            flag:true,
            id: 0,
            key:0,
            fcid:null

        };
    },
    components: {
        SearchBar,
        BottomArea
    },
    computed: {
        ...mapState(["types"])
    },
    methods: {
        changetype(a, b, c) {
            this.loadTypeRight(a);
            this.id = b;
            this.loadListData({arg:c});
            this.fcid=c;
        },
        changeListData(a,b){
            this.loadListData({arg:this.fcid,sort:a});
            this.key = b;
        },
        loadSearchList(a,b){
            let start=a.indexOf("fcid")+5;
            let end=a.indexOf("&");
            let arg=a.substring(start,end);
            // this.loadAreaQuery(arg,b);
            this.$router.push("/search?fcid="+arg+"&title="+b);

        },
        ...mapActions(["loadTypeData", "loadTypeRight","loadListData","loadAreaQuery"])
    },
    mounted() {
        this.loadTypeData();
        this.loadTypeRight();
        this.loadListData();
        this.$root.$on("pipeData",(data)=>{
            this.flag=data.flag;
        })
    }
};
</script>
<style lang="less" scoped>
.clearfixed {
    position: absolute;
    top: 92px; /*px*/
    bottom: 98px; /*px*/
    left: 0px;
    right: 0px;
    .left {
        width: 180px;
        height: 100%;
        float: left;
        background-color: #f6f6f6;
        .content-left {
            width: 100%;
            height: 100%;
            div {
                width: 100%;
                height: 90px; /*px*/
                line-height: 30px; /*px*/
                text-align: center;
                padding: 30px 0px; /*px*/
                p {
                    width: 100%;
                    font-size: 28px; /*px*/
                    height: 30px; /*px*/
                    color: #666;
                }
            }
        }
    }
    .right {
        width: 570px;
        height: 100%;
        float: left;
        background-color: #fff;
        .content-right {
            width: 100%;
            height: 100%;
            padding: 30px 20px 0 20px;
            .types {
                width: 100%;
                height: 100%;
                margin-bottom: 40px; /*px*/
                .type {
                    width: 33.33%;
                    height: 190px; /*px*/
                    float: left;
                    margin: 20px 0px; /*px*/
                    div {
                        width: 130px; /*px*/
                        height: 130px; /*px*/
                        margin: 0 auto;
                        img {
                            width: 100%;
                            height: 100%;
                        }
                    }
                    p {
                        font-size: 24px; /*px*/
                        color: #666666;
                        text-align: center;
                        line-height: 38px; /*px*/
                        margin-top: 20px; /*px*/
                    }
                }
            }
            .classify {
                width: 100%;
                height: 80px; /*px*/
                border-top: 1px solid #eaeaea;
                border-bottom: 1px solid #eaeaea;
                ul {
                    width: 100%;
                    height: 100%;
                    padding: 26px;
                    li {
                        float: left;
                        width: 33.33%;
                        text-align: center;
                        line-height: 28px; /*px*/
                        font-size: 28px; /*px*/
                        border-right: 1px solid #eaeaea;
                    }
                    li:last-child {
                        border: 0px;
                    }
                }
            }
        }
    }
}
.a {
    background: #fff;
    p {
        border-left: solid #ff5577 8px;
        color: #ff5577 !important;
    }
}
.b{
    color:#ff5577 !important;
}
.guessLikeInter {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .oneGood {
        width: 260px;
        height: auto; /*px*/
        margin-top: 18px;
        border-radius: 10px;
        position: relative;
        .saleCount{
            width:auto;
            height:40px;/*px*/
            line-height: 40px;
            position:absolute;
            z-index: 50;
            top:300px;/*px*/
            background:linear-gradient(to right,rgba(0,0,0,1),rgba(0,0,0,0));
            color:#fff;
        }
        .img-wrap {
            width: 260px;
            height: 355px; /*px*/
            img {
                width: 100%;
                height: 100%;
            }
        }
        .good-title {
            width: 100%;
            height: 150px; /*px*/
            padding: 18px; /*px*/
            .info {
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                overflow: hidden;
                line-height: 36px;
            }
            .price {
                position: relative;
                margin-top: 15px; /*px*/
                .like {
                    position: absolute;
                    right: 5px;
                }
            }
        }
    }
}
</style>





