<template>
    <div class="hotClassify">
        <h2>热门分类</h2>
        <div class="goodType">
            <div v-for="(v,i) of dataArr" :key="'item'+i">
                <div class="img">
                    <img :src="v.image" alt="">
                </div>
                <div class="title">{{ v.title }}</div>
            </div>
            
        </div>
    </div>
</template>
<script>
export default {
    props:{
        dataArr:null
    }
    
}
</script>
<style lang="less" scoped>
.hotClassify{
    padding-top:20px;/*px*/
    width:100%;
    background-color: white;
    >h2{
        margin:30px auto;/*px*/
        width:100%;
        text-align: center;
        font-size: 28px;/*px*/
        font-weight: normal;
        background: url("/static/img/background.png") no-repeat;
        background-position: center;
        background-size: 500px 18px;/*px*/
    }
    .goodType{
        width: 100%;
        background-color: #fff;
        display: flex;
        flex-wrap: wrap;
        >div{
            width: 25%;
            margin-top:30px;/*px*/
            height:186px;/*px*/
            .img{
                width:120px;/*px*/
                height:120px;/*px*/
                position: relative;
                left:34px;
                img{
                    width:100%;
                    height:100%;
                }
            }
            .title{
                width:100%;
                height:36px;/*px*/
                line-height: 36px;
                text-align: center;
                color: rgb(96, 96, 96);
                font-size: 25px;/*px*/
                margin-top:20px;/*px*/
            }
        }
    }
    
}
</style>





