import api from '../../server/api';

const state = {
    typeLeft:[],
    typeRight:[],
    sortFilter:[],
    listData:[]
}

const getters = {

}

const actions = {
    loadTypeData({ commit }){
        api.loadDatasByPids({
            appPlat:"m",
            pids:"41789"
        }).then(({ data })=>{
            commit("setTypeLeft",data[41789].list);
        })
    },
    loadTypeRight({commit},arg=41888){
        api.loadTypeRight({
            appPlat:"m",
            pid:arg
        }).then(({ data })=>{
            commit("setTypeRight",data.categoryNavigation.list);
        })
    },
    loadListData({commit},params={arg:10062603,sort:"pop"}){
        api.loadGuessLike({
            fcid:params.arg,
            page:1,
            sort:params.sort,
            cKey:"h5-cube"
        }).then(({ result })=>{
           commit("setSortFilter",result.sortFilter)
           commit("setListData",result.wall.docs);
        })
    }

}

const mutations = {
   
    setTypeLeft(state,data){
        state.typeLeft = data;
    },
    setTypeRight(state,data){
        state.typeRight = data;
    },
    setSortFilter(state,data){
        state.sortFilter = data;
    },
    setListData(state,data){
        state.listData = data;
    }
   
}

export default {
    state,
    actions,
    getters,
    mutations
}


