import api from '../../server/api';

const state = {
    hotWord:[]
}

const getters = {

}

const actions = {
    loadHotWord({ commit }){
        api.loadHotSearch({
            platform:"H5",
            code:"hotWord,sketch"
        }).then(( {data:{hotWord:{data}}} )=>{
           
            commit("setHotWord",data);
        })
    }
   
    

}

const mutations = {
   
    setHotWord(state,data){
        state.hotWord = data;
    }
   
}

export default {
    state,
    actions,
    getters,
    mutations
}


