<template>
    <div class="salesPromotion">
        <h2>超实惠-促销直达</h2>
        <div class="salesInfo">
            <!-- 修改 -->
            <div v-for="(v,i) of dataArr" :key="i" :class="i>=3?'item':'item'+i">
                <div class="desc">
                    <h2>{{ v. title }}</h2>
                    <p>{{ v.viceTitle | format }}</p>
                </div>
                <div class="img">
                    <img :src="v.image" alt="">
                </div>
            </div>
            
        </div>
    
    </div>
</template>
<script>
export default {
    props:{
        dataArr:null
    },
    filters:{
        format(data){
            data=data.replace(/^\{/,"");
            return data=data.replace(/\}$/,"");
        }
    }
}
</script>
<style lang="less" scoped>
@import url('../lessVar.less');
    .salesPromotion{
        padding-top:20px;/*px*/
        width:100%;
        background-color:@white;
        >h2{
            margin:30px auto;/*px*/
            width:100%;
            text-align: center;
            font-size: 28px;/*px*/
            font-weight: normal;
            background: url("/static/img/background.png") no-repeat;
            background-position: center;
            background-size: 500px 18px;/*px*/
        }
        .salesInfo{
            width:100%;
            height:580px;/*px*/
            font-size: 0px;
            .item{
                width: 100%/3;
                height:260px;
                display: inline-block;
                border-top:@border;
                border-right:@border;
                border-bottom:@border;
                position: relative;
                 .desc{
                    text-align: center;
                    padding-top:20px;/*px*/
                }
                .img{
                    width:160px;
                    height:160px;/*px*/ 
                    bottom:0px;
                    left:45px;
                    img{
                        width: 100%;
                        height: 100%;
                    }
                    
                }
            }
            .item0{
                height:330px;
                width: 100%/2;
                // display: inline-block;
                float: left;
                border-top:@border;
                border-right:@border;
                position: relative;
                .desc{
                    text-align: center;
                    padding-top:30px;/*px*/

                }
                .img{
                    width:200px;
                    height: 200px;
                    bottom:0px;
                    left:87.5px;

                    img{
                        width:100%;
                        height:100%;
                    }
                }
            }
            .item1,.item2{
                height:330px/2;
                width: 100%/2;
                // display: inline-block;
                float: left;
                border-top:@border;
                position: relative;
                padding-left:10px;
                .desc{
                    position: relative;
                    top:40px;
                }
                .img{
                    width: 160px;
                    height: 160px;
                    right:0px;
                    bottom:0px;
                    img{
                        width: 100%;
                        height: 100%;
                    }
                }
            }

        }

    }
    .desc{
        >h2{
            font-size: 28px;/*px*/
            color:#333;
        }
        p{
            font-size: 24px;/*px*/
            color:#ff5f7e;

        }
    }
    .img{
        position: absolute;
       
    }
</style>





