<template>
    <div>
        <search-bar></search-bar>
        <div class="clearfixed" v-show="flag">
             <scroll-view class="wrapper">
                <div class="content">
                    <carousel-area :dataArr="home['warpImgs']"></carousel-area>
                    <good-type :dataArr="home['types']"></good-type>
                    
                    <SalesPromotion :dataArr="home['bebefit']"/>
                    <hot-classify :dataArr="home['hotType']"></hot-classify>
                    <guess-like :dataArr="home['likes']"></guess-like>
        
                </div>
            </scroll-view>
        </div>
        <bottom-area></bottom-area>
    </div>
</template>
<script>
import SearchBar from "./SearchBar";
import BottomArea from "./BottomArea";
import BScroll from "better-scroll";
import CarouselArea from "./CarouselArea";
import GoodType from "./GoodType";
import SalesPromotion from "./SalesPromotion";
import HotClassify from "./HotClassify";
import GuessLike from "./GuessLike"

import jsonp from "fetch-jsonp";
import api from "../server/api";

import {mapState,mapMutations,mapActions,mapGetters} from "vuex";
export default {
    data(){
        return{
            flag:true
        }
    },
    components: {
        SearchBar,
        BottomArea,
        CarouselArea,
        GoodType,
        SalesPromotion,
        HotClassify,
        GuessLike
    },
    computed:{
        ...mapState(['home'])
    },
    methods: {
        ...mapActions(["loadHomeData","loadGuessLike"])
      
    },
    mounted() {  
        this.loadHomeData();
        this.loadGuessLike();
        this.$root.$on("pipeData",(data)=>{
            this.flag=data.flag;
        })
    }
};
</script>
<style lang="less" scoped>
@import url('../lessVar.less');
.clearfixed {
  position: absolute;
  top: @topHeight; /*px*/
  bottom: @bottomHeight; /*px*/
  left: 0px;
  right: 0px;
  .wrapper {
    width: 100%;
    height: 100%;
  }
}
.guessLikeOuter{
    width:100%;
    background-color:@white;
    padding:20px 18px;/*px*/
    >h2{
        margin:30px auto;/*px*/
        width:100%;
        text-align: center;
        font-size: 28px;/*px*/
        font-weight: normal;
        background: url("/static/img/background.png") no-repeat;
        background-position: center;
        background-size: 500px 18px;/*px*/
    }
    .guessLikeInter{
        width:100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        .oneGood{
            width:348px;
            height:614px;/*px*/
            margin-top:18px;
            border-radius: 10px;
            

        }
    }
}
.types{
    width: 100%;
    background-color: #fff;
    display: flex;
    flex-wrap: wrap;
}
.hotClassify{
    padding-top:20px;/*px*/
    width:100%;
    background-color: white;
    >h2{
        margin:30px auto;/*px*/
        width:100%;
        text-align: center;
        font-size: 28px;/*px*/
        font-weight: normal;
    }
    .types{
        .goodType{
            width: 25%;
            height:180px;/*px*/
           .img{
                width:120px;
                height:120px;
            }
        }
    }
    
}
</style>





