<template>
  <div class="searchList">
    <search-bar v-show="!flag"></search-bar>
    <div v-show="flag">
      <div class="header" @click="changeFlag()">
        <div class="search">
          <i class="iconfont icon-sousuo"></i>
        </div>
        <i class="iconfont icon-gouwuche"></i>
      </div>
      <div class="classify">
        <ul>
          <li v-for="(v,i) of list.sortFil" :key="'item'+i" @click="loadSelectData(v.sortKey,i)" :class="{c:key==i}">{{ v.title }}</li>
        </ul>
      </div>
      <div class="clear">
        <scroll-view class="wrapper">
          <div class="content">
            <div class="guessLikeInter">
              <div class="oneGood" @click="goToDetail()" v-for="(v,i) of list.areaData" :key="'item'+i">
                <div class="img-wrap">
                  <img :src="v.img" alt="">
                </div>
                <div class="good-title">
                  <div class="info">
                    {{ v.title }}
                  </div>
                  <div class="price">
                    <span>
                      <i>￥</i>{{ v.price }}</span>
                    <span class="like">{{ v.cfav }}
                      <i class="iconfont icon-shoucang"></i>
                    </span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </scroll-view>
      </div>
    </div>

  </div>
</template>
<script>
import SearchBar from "./SearchBar";
import { mapState, mapMutations, mapActions, mapGetters } from "vuex";
export default {
    data() {
        return {
            key: 0,
            flag: true
        };
    },
    components: {
        SearchBar
    },
    computed: {
        ...mapState(["list"])
    },
    methods: {
        changeFlag() {
            this.flag = !this.flag;
            this.$root.$emit("pipe", {
                flag: this.flag
            });
        },
        loadSelectData(a, b) {
            this.key = b;
            if (this.$route.query.q) {
                this.loadlistData({ q: this.$route.query.q, sort: "pop" });
            } else {
                this.loadAreaQuery({
                    fcid: this.$route.query.fcid,
                    title: this.$route.query.title,
                    sort: "pop"
                });
            }
        },
        ...mapActions(["loadAreaQuery", "loadlistData"])
    },
    mounted() {
        if (this.$route.query.q) {
            this.loadlistData({ q: this.$route.query.q, sort: "pop" });
        } else {
            this.loadAreaQuery({
                fcid: this.$route.query.fcid,
                title: this.$route.query.title,
                sort: "pop"
            });
        }
        this.$root.$on("pipeData", data => {
            this.flag = data.flag;
        });
    },
    beforeRouteUpdate(to, from, next) {
        this.loadlistData({q:to.query.q,sort:"pop"});
        next();
    }
};
</script>
<style lang="less" scoped>
.c {
    color: #f9446b;
}
.searchList {
    position: relative;
    background-color: #fff;
    .header {
        width: 100%;
        height: 90px;
        padding: 15px 0 15px 20px; /*px*/
        border-bottom: 1px solid #eaeaea;
        .search {
            width: 85%;
            height: 100%;
            line-height: 60px; /*px*/
            padding-left: 10px;
            float: left;
            background: #f6f6f6;
        }
        > i {
            float: left;
            width: 15%;
            text-align: center;
            line-height: 60px;
            font-size: 45px; /*px*/
        }
    }
    .classify {
        width: 100%;
        height: 80px; /*px*/
        border-bottom: 1px solid #eaeaea;
        ul {
            width: 100%;
            height: 100%;
            padding: 25px 0px;
            li {
                float: left;
                width: 33.33%;
                height: 100%;
                text-align: center;
                border-right: 1px solid #eaeaea;
                &:last-child {
                    border: 0px;
                }
            }
        }
    }
    .clear {
        position: absolute;
        top: 170px;
        left: 0;
        right: 0;
        bottom: 0;
        .wrapper {
            position: absolute;
            width: 100%;
            height: 100%; /*px*/
        }
    }
}
.guessLikeInter {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0px 20px;
    .oneGood {
        width: 348px;
        height: 614px; /*px*/
        margin-top: 18px;
        border-radius: 10px;

        .img-wrap {
            width: 348px;
            height: 464px; /*px*/
            img {
                width: 100%;
                height: 100%;
            }
        }
        .good-title {
            width: 100%;
            height: 150px; /*px*/
            padding: 18px; /*px*/
            .info {
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                overflow: hidden;
                line-height: 36px;
            }
            .price {
                position: relative;
                margin-top: 15px; /*px*/
                .like {
                    position: absolute;
                    right: 5px;
                }
            }
        }
    }
}
</style>






