<template>
    <div class="seachBar">
        <div class="searchOne" v-show="flag">
            <a class="iconfont icon-xiaoxizhongxin"></a>
            <div @click="changeFlag()">
                <i class="iconfont icon-sousuo"></i>
                <span>口红</span>
            </div>
            <a href="" class="iconfont icon-gouwuche"></a>
        </div>
        <div class="header" v-show="!flag">
            <span class="iconfont icon-xiangzuojiantou" @click="changeFlag()"></span>
            <form action="">
                <input type="text" placeholder="裙子" v-model="tag">
                <a @click="goToList(tag)">搜索</a>
            </form>
        </div>
        <transition-group enter-active-class="animated fadeInRight" leave-active-class="animated fadeOutLeft">
            <div class="content" v-show="!flag" :key="1">
                <div class="history">
                    <div class="title">
                        <span>
                            <i class="iconfont icon-sousuo"></i> 历史搜索</span>
                        <span class="iconfont icon-lajixiang" @click="clear()"></span>
                    </div>
                    <div class="info">
                        <ul>
                            <li v-for="(v,i) of historyKeyWord" :key="'item'+i" @click="goToList(v)">{{ v }}</li>
                        </ul>
                        <div v-show="historyKeyWord.length==0">
                            暂无历史记录搜索
                        </div>
                    </div>
                </div>
                <div class="hotwords">
                    <div>
                        <i class="iconfont icon-shoucang"></i>
                        <span>热门搜索</span>
                        <div>
                            <ul>
                                <li v-for="(v,i) of search.hotWord" :key="'item'+i" @click="goToList(v.query)">{{ v.query }}</li>

                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </transition-group>

    </div>
</template>
<script>
import { mapState, mapMutations, mapActions, mapGetters } from "vuex";
export default {
    data() {
        return {
            flag: true,
            tag: "",
            historyKeyWord: []
        };
    },
    methods: {
        changeFlag() {
            this.flag = !this.flag;
            this.$root.$emit("pipeData", {
                flag: this.flag
            });
        },
        goToList(a) {
            this.flag = !this.flag;
            this.$root.$emit("pipeData", {
                flag: this.flag
            });
            this.$router.push("/search?q=" + a);
            if (!localStorage.historyKeyWord) {
                localStorage.historyKeyWord = "";
            }
            localStorage.historyKeyWord += a + ",";
            var b = localStorage.historyKeyWord.split(",");
            b.pop();
            let c=[...new Set(b)]
            this.historyKeyWord = c;
        },
        clear() {
            localStorage.clear();
            this.historyKeyWord = [];
        },
        ...mapActions(["loadHotWord"])
    },
    computed: {
        ...mapState(["search"])
    },
    mounted() {
        this.loadHotWord();
        this.$root.$on("pipe", data => {
            this.flag = data.flag;
        });
        var b = localStorage.historyKeyWord.split(",");
        b.pop();
        let c=[...new Set(b)]
        this.historyKeyWord = c;
    }
};
</script>
<style lang="less" scoped>
.seachBar {
    width: 100%;
    height: 100%;
    background: #fff;
    .searchOne {
        display: flex;
        width: 100%;
        height: 92px; /*px*/
        padding: 15px 10px;
        background-color: #fff;
        border-bottom: 1px solid #e5e5e5;
        z-index: 100;
        position: fixed;
        top: 0;
        a {
            display: inline-block;
            font-size: 44px; /*px*/
            text-align: center;
            width: 100px;
            height: 100%;
            line-height: 62px;
        }
        div {
            width: 630px;
            height: 60px; /*px*/
            line-height: 60px; /*px*/
            font-size: 24px;
            border: none;
            border-radius: 6px;
            background-color: #eee;
            color: #999;
            padding: 0px 12px;
        }
    }
}

.header {
    width: 100%;
    height: 90px; /*px*/
    border-bottom: 1px solid #eee;
    padding: 10px 0px; /*px*/
    position: relative;
    span {
        display: inline-block;
        position: absolute;
        left: 15px;
        top: 25px; /*px*/
        color: #666;
        width: 7%;
        height: 40px; /*px*/
        line-height: 40px; /*px*/
    }
    form {
        position: absolute;
        width: 89%;
        height: 50px; /*px*/
        left: 10%;
        top: 24%;
        input {
            padding: 10px 20px; /*px*/
            float: left;
            width: 590px;
            height: 50px; /*px*/
            box-shadow: none;
            border-radius: 9px;
            border: 1px solid #aaa;
            font-size: 28px; /*px*/
            line-height: 50px; /*px*/
            vertical-align: middle;
            background: transparent;
            background-color: #fff;
            outline: none;
            &:focus {
                border: 2px solid #f50438;
            }
        }
        a {
            position: absolute;
            height: 50px; /*px*/
            width: 70px;
            right: 0;
            font-size: 28px; /*px*/
            line-height: 50px; /*px*/
            text-align: center;
        }
    }
}
.content {
    width: 100%;
    .history {
        width: 100%;
        padding: 20px; /*px*/
        color: #999;
        min-height: 60px; /*px*/
        border-bottom: 1px solid #eee;
        .title {
            font-size: 25px; /*px*/
            line-height: 45px; /*px*/
            height: 45px; /*px*/
            span:last-child {
                float: right;
                margin-right: 20px;
            }
        }
        .info {
            ul {
                padding: 30px 30px 0 30px;
                li {
                    display: inline-block;
                    height: 50px; /*px*/
                    border: 1px solid #eee;
                    padding: 10px 20px;
                    line-height: 30px; /*px*/
                    font-size: 28px;
                    border-radius: 10px;
                    margin-right: 10px;
                    margin-bottom: 20px;
                }
            }
            > div {
                font-size: 25px; /*px*/
                line-height: 45px; /*px*/
                height: 45px; /*px*/
                margin-top: 20px; /*px*/
                margin-left: 35px;
            }
        }
    }
    .hotwords {
        width: 100%;
        min-height: 60px; /*px*/
        overflow: hidden;
        padding: 20px;
        border-top: 1px solid #f4f4f4;
        color: #999;
        ul {
            padding: 30px 30px;
            li {
                display: inline-block;
                height: 50px; /*px*/
                border: 1px solid #eee;
                padding: 10px 20px;
                line-height: 30px; /*px*/
                font-size: 28px;
                border-radius: 10px;
                margin-right: 10px;
                margin-bottom: 20px;
            }
            li:nth-child(3),
            li:nth-child(5),
            li:nth-child(7) {
                color: #f04d71;
            }
        }
    }
}
</style>





