<template>
    <div class="goodType">
        <div v-for="(v,i) of dataArr" :key="'item'+i">
            <div class="img">
                <img :src="v.image" alt="">
            </div>
            <div class="title">{{ v.title }}</div>
        </div>
        
    </div>
   
</template>
<script>
export default {
    data(){
        return {
            
        }
    },
    props:{
        dataArr:null
    },
}
</script>
<style lang="less" scoped>
 .goodType{
    width: 100%;
    background-color: #fff;
    display: flex;
    flex-wrap: wrap;
    >div{
        width: 20%;
        margin-top:30px;/*px*/
        height:136px;/*px*/
        .img{
            width:100px;/*px*/
            height:100px;/*px*/
            position: relative;
            left:25px;
            border-radius: 50px;

            img{
                width:100%;
                height:100%;
            }
        }
        .title{
            width:100%;
            height:36px;/*px*/
            line-height: 36px;
            text-align: center;
            color: rgb(96, 96, 96);
            font-size: 25px;/*px*/
        }
    }
}
    
    
    
</style>





