<template>
    <div class="wrap">
        <swiper :options="swiperOption" ref="mySwiper">
            <!-- slides -->
            <swiper-slide v-for="(v,i) of dataArr" :key="'item'+i">
                <img :src="v.image_800" alt="">
            </swiper-slide>
            <!-- Optional controls -->
            <div class="swiper-pagination"  slot="pagination"></div>

        </swiper>
    </div>
</template>
<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper';

export default {
    components:{
        swiper,
        swiperSlide
    },
    data(){
      return{
            swiperOption:{
                autoplay:2000,
                loop:true,
                pagination:".swiper-pagination",
                paginationClickable:true
            },
            
        }
    },
    props:{
        dataArr:null
    },
    methods:{
        
    },
    mounted(){
       
    }
    
}
</script>
<style lang="less" scoped>

</style>





