<template>
    <div class="goodDetail">
        <div class="clear">
            <scroll-view class="wrapper">
                <div class="content">
                    <div class="goodInfo">
                        <div class="imgs">
                            <img :src="good.img" alt="">
                        </div>
                        <div class="desc">
                           {{ good.title }}
                        </div>
                        <div class="price">
                            <span>¥</span>
                            <span class="newPrice">{{ good.price }}</span>
                            <div class="oldPrice">
                                <span>¥ <i>{{ good.orgPrice }}</i></span>
                                <p>优惠价</p>
                            </div>
                        </div>
                    </div>
                    <div class="comment">
                        <div class="title">
                            <span>买家评论
                                <i>{{ comment.userList.length }}</i>
                            </span> |
                            <span>销量
                                <i>{{ good.cfav }}</i>
                            </span>
                        </div>
                        <div class="tags">
                            <span v-for="(v,i) of comment.rateTags" :key="'item'+i">{{ v.value }}({{ v.num }})</span>
                            
                        </div>
                        <div class="info">
                            <div class="com" v-for="(v,i) of comment.userList" :key="'item'+i">
                                <div class="userInfo">
                                    <div class="headImg">
                                        <img :src="v.user.avatar" alt="">
                                    </div>
                                    <div class="userName">
                                        {{v.user.uname }}
                                    </div>
                                </div>
                                <div class="message">
                                    {{ v.content }}
                                </div>
                                <div class="time">
                                    {{v.time }}
                                </div>
                            </div>
                        </div>
                    </div>

                </div>
            </scroll-view>
        </div>

        <div class="footer">
            <div class="shop">
                <span class="iconfont icon-shoucang"></span>
                <p>收藏</p>
            </div>
            <div class="shop">
                <span class="iconfont icon-gouwuche"></span>
                <p>小店</p>
            </div>
            <div class="cart" @click="addToCart(flag)">
                加入购物车
            </div>
            <div class="cart">

                立即购买
            </div>
        </div>
    </div>
</template>
<script>
import {mapState,mapMutations,mapActions,mapGetters} from "vuex";
export default {
    data(){
        return{
            good:{},
            flag:false
        }
    },
    computed:{
        ...mapState(['comment'])
    },
    methods: {
        ...mapActions(["loadComment"]),
        addToCart(a){
            this.$router.push("/cart?flag="+a);
        }
      
    },
    mounted() {  
        this.loadComment(this.$route.query.itemId); 
        this.good=JSON.parse(sessionStorage.det);
    }
};
</script>
<style lang="less" scoped>
.goodDetail {
    width: 100%;
    height: 100%;
    position: relative;
    .clear {
        position: absolute;
        top: 0;
        bottom: 100px;
        left: 0;
        right: 0;
        .wrapper {
            width: 100%;
            height: 100%;
            .goodInfo {
                width: 100%;
                height: 1050px; /*px*/
                padding: 20px 20px;
                background-color: white;
                .imgs {
                    width: 100%;
                    height: 810px; /*px*/
                    img{
                        width: 100%;
                        height: 100%;
                    }
                }
                .desc {
                    width: 100%;
                    height: 84px;
                    line-height: 42px;
                    margin-top: 30px;
                    font-size: 32px; /*px*/
                    display: -webkit-box;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient: vertical;
                    overflow: hidden;
                }
                .price {
                    width: 100%;
                    height: 108px; /*px*/
                    position: relative;
                    > span:first-child {
                        position: absolute;
                        top: 15px; /*px*/
                    }
                    .newPrice {
                        font-size: 60px;
                        padding-left: 16px;
                        margin-right: 20px;
                    }
                    .oldPrice {
                        position: absolute;
                        bottom: 30px;
                        display: inline-block;
                        span {
                            color: #999;
                            text-decoration: line-through;
                        }
                        p {
                            background-color: #f5d0d8;
                            color: #f9446b;
                        }
                    }
                }
            }
            .comment {
                margin-top: 30px;
                width: 100%;
                background: white;
                padding: 20px 20px;
                color: #999;
                .title {
                    width: 100%;
                    height: 40px; /*px*/
                }
                .tags {
                    width: 100%;
                    margin-top:10px;
                    span {
                        display: inline-block;
                        width: auto;
                        height: 48px; /*px*/
                        background-color: #f5d0d8;
                        color: #f9446b;
                        padding: 8px 10px;
                        border-radius: 8px;
                        margin-right:10px;
                    }
                    span:nth-child(3){
                        background-color: #eaeaea;
                        color: #666;
                    }
                }
                .info {
                    width: 100%;
                    height: auto;
                    .com {
                        border-bottom: 1px solid #eaeaea;
                        .userInfo {
                            width: 100%;
                            height: 80px; /*px*/
                            margin-top:20px;/*px*/
                            .headImg {
                                float: left;
                                width: 80px;
                                height: 80px; /*px*/
                                border-radius: 40px;
                                margin-right: 20px;
                                .img {
                                    width: 100%;
                                    height: 100%;
                                }
                            }
                            .userName {
                                float: left;
                                height: 80px; /*px*/
                                line-height: 80px; /*px*/
                                color:#333;
                            }
                        }
                        .message {
                            width: 100%;
                            // height: 80px;
                            line-height: 40px;
                            margin-top: 10px;/*px*/
                            font-size:28px; /*px*/
                            display: -webkit-box;
                            -webkit-line-clamp: 2;
                            -webkit-box-orient: vertical;
                            overflow: hidden;
                        }
                        .time{
                            width:100%;
                            height:40px;
                            margin:20px 0px 40px auto;
                        }
                    }
                }
            }
        }
    }

    .footer {
        background: #fff;
        width: 100%;
        height: 100px;
        position: fixed;
        bottom: 0px;
        border-top: 1px solid #eaeaea;
        > .shop {
            float: left;
            width: 160px;
            height: 100%;
            text-align: center;
            padding-top: 20px; /*px*/
            color: rgb(44, 43, 43);
            border-right: 1px solid #eaeaea;
            > span {
                font-size: 30px; /*px*/
            }
        }
        > .cart {
            float: left;
            width: 215px;
            height: 100%;
            line-height: 100%;
            text-align: center;
            padding: 30px 0px;
            font-size: 30px;
            color: white;
            background-color: #f9446b;
            border-right: 1px solid #eaeaea;
            &:last-child {
                border: 0px;
            }
        }
    }
}
</style>





